<template>
    <div class="todo-item" v-bind:class="{ 'is-completed' : todo.completed }">

        <p>
            <input type="checkbox" v-on:change="todocompleted" v-bind:checked="todo.completed" > 
            {{ todo.title }}
            <button class="del" v-on:click="$emit('del-todo',todo.id)">X</button>
        </p>
    </div>
</template>

<script>
    export default {
        name : 'TodoItem',
        props: {
            todo: {
             
                
            },
        },
        methods : {
            todocompleted()
            {
                // console.log(this.todo.id);
                console.log(this.todo.id);
                this.todo.completed =   !this.todo.completed;
            }
        }
    }
</script>

<style  scoped>
    .todo-item
    {
        background: #f4f4f4;
        padding: 10px;
        border-bottom: 1px #ccc dotted;
    }
  
    .is-completed
    {
        text-decoration: line-through;
    }
    .del
    {
        background: #ff0000;
        color: #fff;
        border:none;
        padding: 5px 10px;
        border-radius : 50%;
        cursor: pointer;
        float: right;
    }
</style>