<template>
    <div>
        <form action="" @submit.prevent="addTodo">
            <input type="text" name="title" placeholder="Add Todo..." v-model="title" >
            <input type="submit" value="Submit" class="btn"> 
        </form>
    </div>
</template>


<script>

// import {v4}  from 'uuid';

    export default {
        name : "AddTodo",
        data() {
            return {
                title: null
            }
        },
        methods : {
            addTodo()
            {
                
                const newTodo = {
                    // id : v4(),
                    title : this.title,
                    completed : false
                }
                //send to parent by emitting an event
                this.$emit('add-todo',newTodo);
                this.title = null 
            }
        }
    }
</script>

<style  scoped>
    form
    {
        display: flex;
    }
    input[type="text"]
    {
        flex : 10;
        padding : 5px;
    }
    input[type="submit"]
    {
        flex: 2;
    }
</style>